import React from 'react';
import { createMuiTheme, ThemeProvider } from "@material-ui/core/styles";
import { Container } from '@material-ui/core';
import * as colors from "@material-ui/core/colors";
import CssBaseline from "@material-ui/core/CssBaseline";
import EnglishData from './context/EnglishData';
import 'fontsource-roboto';
import Word from './components/Word';
import Sentence from './components/Sentence';
import Paginate from './components/Pagination';
import Part from './components/Part';
import Menu from './components/Menu';

interface AppProps {
}

interface AppState {
  slides: {
    part: string;
    word: string;
    partOfSpeech: string;
    trans: string;
    sentence: string;
    sentenceTrans: string;
  }[];
  currentPart : string;
  wordIndex: number;
  speechSpeedRate: number;
  visibleTrans: string;
  visibleSentenceTrans: string;
  isInputWordCompleted: boolean;
  inputWordString: string;
}

class App extends React.Component<AppProps, AppState> {
  constructor(props: AppProps) {
    super(props);
    
    this.state = {
      slides: EnglishData.WORDS.filter(w => w.part === 'toeic1'),
      currentPart: 'toeic1',
      wordIndex: 0,
      speechSpeedRate: 0.8,
      visibleTrans: 'translate-hidden',
      visibleSentenceTrans: 'translate-hidden',
      isInputWordCompleted: false,
      inputWordString: '',
    }
  }

  render () {
    const theme = createMuiTheme({
      palette: {
        primary: {
          main: colors.blue[800],
        },
        type: 'dark',
      },
    });

    const slidePrev = () => {
      if (this.state.wordIndex > 0) {
        this.setState({
          wordIndex: this.state.wordIndex - 1,
          visibleTrans: 'translate-hidden',
          visibleSentenceTrans: 'translate-hidden',
          isInputWordCompleted: false,
          inputWordString: ''
        })
      }
    }
  
    const slideNext = () => {
      if (this.state.wordIndex < this.state.slides.length-1) {
        this.setState({
          wordIndex: this.state.wordIndex + 1,
          visibleTrans: 'translate-hidden',
          visibleSentenceTrans: 'translate-hidden',
          isInputWordCompleted: false,
          inputWordString: ''
        })
      }
    }

    const changeInputWordCompleted = (val: boolean) => {
      this.setState({
        isInputWordCompleted: val
      });
    }

    const changeInputWord = (val: string) => {
      this.setState({inputWordString: val})
    }

    const changeCurrentPart = (val: string) => {
      if (this.state.currentPart !== val) {

        const s = EnglishData.WORDS.filter(w => w.part === val);

        this.setState({
          slides: s,
          currentPart: val,
          wordIndex: 0,
          visibleTrans: 'translate-hidden',
          visibleSentenceTrans: 'translate-hidden',
          isInputWordCompleted: false,
          inputWordString: ''
        })
        }
    }

    const changeSpeechSpeedRate = (val: number) => {
      this.setState({speechSpeedRate: val});
    }
    
    const slides = this.state.slides;
    const wordIndex = this.state.wordIndex ?? 0;
    const visibleTrans = this.state.visibleTrans ?? 'translate-hidden';
    const visibleSentenceTrans = this.state.visibleSentenceTrans ?? 'translate-hidden';
    const isCompleted = this.state.isInputWordCompleted;
    const inputWordString = this.state.inputWordString;
    const speechSpeedRate = this.state.speechSpeedRate;

    return (
      <ThemeProvider theme={theme}>
        <CssBaseline />
        <Menu
            currentPart={this.state.currentPart}
            changeCurrentPart={(val) => changeCurrentPart(val)}
            speechSpeedRate={this.state.speechSpeedRate}
            changeSpeechSpeedRate={(val) => changeSpeechSpeedRate(val)}
        />
        <Container maxWidth="sm">
          <Paginate
            onClickPrev={slidePrev}
            onClickNext={slideNext}
          />
          <hr/>
          <Word
            word={slides[wordIndex].word}
            partOfSpeech={slides[wordIndex].partOfSpeech}
            trans={slides[wordIndex].trans}
            visibleTrans = {visibleTrans}
            isInputWordCompleted={isCompleted}
            inputWordString={inputWordString}
            onClick={() => this.setState({visibleTrans:'translate-visible'})}
            changeInputWordCompleted={(val) => changeInputWordCompleted(val)}
            changeInputWord={(val) => changeInputWord(val)}
          />
          <hr/>
          <Sentence
            word={slides[wordIndex].sentence}
            trans={slides[wordIndex].sentenceTrans}
            visibleTrans={visibleSentenceTrans}
            speechSpeedRate={speechSpeedRate}
            onClick = {() => this.setState({visibleSentenceTrans:'translate-visible'})}
          />
          <hr/>
          <Part
            currentPart={this.state.currentPart}
          />

        </Container>
      </ThemeProvider>
    );
  }
}

export default App;