import React, { useState } from 'react';
import { FormControlLabel, Drawer, Switch, IconButton, List, ListItem, Slider } from '@material-ui/core';
import MenuIcon from '@material-ui/icons/Menu';
import { ListItemText, ListItemIcon } from '@material-ui/core';
import CheckIcon from '@material-ui/icons/Check';
import Divider from '@material-ui/core/Divider';
import EnglishData from '../context/EnglishData';

interface MenuProps {
  currentPart: string;
  changeCurrentPart: (val:string) => void;
  speechSpeedRate: number;
  changeSpeechSpeedRate: (val:number) => void;
}

const Menu: React.FC<MenuProps> = (props) => {
  const [menuToggle, setMenuToggle] = useState(false);

  const toggleDrawer = () => {
    setMenuToggle(!menuToggle);
  };

  const parts = EnglishData.getParts();

  return (
    <>
      <IconButton
        color="inherit"
        aria-label="open drawer"
        edge="end"
        onClick={() => toggleDrawer()}
      />
      <Drawer
        anchor='left'
        open={menuToggle}
        onClose={() => setMenuToggle(false)}
      >
        <div>
          <List>
            <ListItem>
              <FormControlLabel
                value="Theme"
                control={<Switch color="primary" />}
                label="Theme"
                labelPlacement="start"
              />
              </ListItem>
          </List>
          <Divider />
          <List component="nav" aria-label="secondary part">
            {
              parts.map(p => {
                return (
                  <ListItem 
                    button
                    onClick={() => {
                      props.changeCurrentPart(p);
                      setMenuToggle(false);
                    }}
                    key={p}
                  >
                    <ListItemText primary={p}/>
                    <ListItemIcon>
                      {
                        p === props.currentPart
                        &&
                        <CheckIcon />
                      }
                    </ListItemIcon>
                  </ListItem>
                )
              })
            }
          </List>
          <Divider />
          <List component="nav" aria-label="thirdry speechSpeed">
            <ListItem>
              <Slider
                min={0.1}
                max={1}
                step={0.1}
                value={props.speechSpeedRate}
                aria-labelledby="speech-speed-rate"
                valueLabelDisplay="auto"
                onChange={(e, v) => {
                  let rate:number = props.speechSpeedRate;
                  if (typeof(v) ===  'number') {
                    rate = v;
                  }
                  props.changeSpeechSpeedRate(rate);
                }}
              />
            </ListItem>
          </List>
        </div>
      </Drawer>
      <MenuIcon />
    </>
  );
}

export default Menu;